#intro {
  height: calc(100vh - 4rem);
  width: 100vw;
  max-width: 75rem;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
  margin-top: 5rem;
}

.bg {
  position: absolute;
  z-index: -1;
  top: -4rem;
  right: 0;
  object-fit: cover;
  height: 100vh;
}

.introContent {
  height: 100vh;
  width: 100vw;
  padding: 2rem;
  font-size: 3rem;
  font-weight: 700;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.hello {
  font-size: 1.75rem;
  font-weight: 100;
}

.introName {
  color: #4895ef;
}

.introPara {
  font-size: medium;
  font-weight: 300;
  letter-spacing: 1px;
  width: 100%;
  max-width: 40rem;
}

.btn {
  background: #fffffe;
  margin: 1rem 0;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 2rem;
}

.btn:hover {
  cursor: pointer;
}

.btnImg {
  object-fit: cover;
  margin: 0 .25rem;
  /* height: 1rem; */
}

@media screen and (max-width: 840px) {
  .bg {
    right: -30vw;
  }

  .introContent {
    font-size: 10vw;
  }

  .hello {
    font-size: 4.5vw;
  }
}

@media screen and (max-width: 500px) {
  .bg {
    right: -50vw;
  }
}