.navbar {
  background: rgb(30, 30, 30);
  height: 5rem;
  width: 100vw;
  max-width: 75rem;
  margin: 0 auto;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 3;
  transition: box-shadow 0.3s ease-in-out;
}

.logo {
  object-fit: cover;
  height: 3rem;
  width: 4rem;
}

.logo:hover {
  cursor: pointer;
}

.desktopMenuListItem {
  margin: 1rem;
  cursor: pointer;
  transition: color 0.3s ease, padding-bottom 0.3s ease, border-bottom 0.3s ease;
  color: #fffffe;
  text-decoration: none;
  border-bottom: 3px solid transparent;
}

.desktopMenuListItem:hover, .desktopMenuListItem.active {
  color: #4895ef;
  padding-bottom: 0.5rem;
  border-bottom: 3px solid #4895ef;
  cursor: pointer;
}

.desktopMenuBtn {
  background-color: #fffffe;
  border: none;
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1rem;
  border-radius: 2rem;
}

.desktopMenuBtn:hover {
  cursor: pointer;
}

.desktopMenuImg{
  object-fit: cover;
  height: 1rem;
  width: 1rem;
  margin: 1rem;
}

.active {
  color: #4895ef;
}

.navMenu {
  position: absolute;
  top: 4rem;
  right: 2rem;
  z-index: 2;
  display: flex;
  flex-direction: column;
  padding: .5rem;
  height: fit-content;
  min-width: 15rem;
  background: rgb(40, 40, 40);
  border-radius: 1rem;
}

.listItem {
  color: #fffffe;
  padding: .5rem 3rem;
  margin: .25rem;
  background: rgb(30, 30, 30);
}

.mobMenu {
  display: none;
  object-fit: cover;
  height: 1.8rem;
}

@media screen and (max-width:720px) {
  .mobMenu {
    display: flex;
  }

  .desktopMenu {
    display: none;
  }

  .desktopMenuBtn{
    display: none;
  }

  .active {
    color: #4895ef;
    padding-bottom: .5rem;
    border-bottom: 3px solid #4895ef;
  }
}